import React from "react";
import { throttle } from "lodash";
import { getMedia } from "../../DataManager/config";

export default class VideoBackground extends React.Component {
  state = {
    videoID: null,
    videoHeight: "100%",
    player: null,
  };

  componentDidMount() {
    window.addEventListener("resize", this.handleResize);
    this.handleResize();
    this.fetchData();
    setTimeout(() => this.setState({ played: true }), 3000);
  }

  fetchData = () => {
    getMedia().then((item) => {
      if (!item) {
        return;
      }
      const data = item.data();
      this.setState({ videoID: data && data.videoID });
    });
  };

  handleResize = throttle(() => {
    this.setState({
      videoWidth: `${window.innerWidth}px`,
      videoHeight: `${(window.innerWidth * 9) / 16}px`,
    });
  }, 100);

  render() {
    console.log("played", this.state.played);
    return (
      <div className="home-wrapper">
        <div className={this.state.className}>
          <div
            className={`muine-video-wrapper ${
              this.state.played ? "hide-wrapper" : ""
            }`}
            style={{
              height: this.state.videoHeight,
              width: this.state.videoWidth,
            }}
          >
            {this.state.videoID && (
              <iframe
                title="Muine Bay"
                className="fadeIn iframe-player"
                src={`https://player.vimeo.com/video/${this.state.videoID}?loop=1&autoplay=1&background=1&muted=1`}
                width="100%"
                height="100%"
                frameBorder="0"
                webkitallowfullscreen=""
                mozallowfullscreen=""
                allowFullScreen="1"
              ></iframe>
            )}
          </div>
        </div>
      </div>
    );
  }
}
